<template>
  <div v-show="showFlag" class="food" ref="food">
    <div class="food-content">
      <div class="image-header">
        <img :src="food.image">
        <div class="back" @click="hide">
          <span class="icon-arrow_lift">&lt;</span>
        </div>
      </div>
      <div class="content">
        <h1 class="title">{{food.name}}</h1>
        <div class="details">
          <span class="sell-count">月售{{food.sellCount}}份</span>
          <span class="rating">好评率{{food.rating}}</span>
        </div>
        <div class="price">
          <span class="now">￥{{food.price}}</span>
          <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
        </div>
        <div class="cartcontrol-wrapper">
          <cartcontrol :food="food"></cartcontrol>
        </div>
        <div @click.stop.prevent="addFirst" class="buy" v-show="!food.count || food.count===0">加入购物车</div>

      </div>
      <split v-show="food.price"></split>
      <div class="info" v-show="food.info">
        <h1 class="title">商品信息</h1>
        <p class="text">{{food.info}}</p>
      </div>
      <split></split>
      <div class="rating">
        <h1 class="title">商品评价</h1>
        <ratingselect :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings"></ratingselect>
        <div class="rating-wrapper">
          <ul v-show="food.ratings && food.ratings.length">
            <li v-show="needShow(rating.rateType,rating.text)" v-for="rating in food.ratings" class="rating-item">
              <div class="user">
                <span class="name">{{rating.username}}</span>
                <img class="avatar" width="12" height="12" :src="rating.avatar">
              </div>
              <div class="time">{{rating.rateTime | formatDate}}</div>
              <p class="text">
                <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}">!</span>{{rating.text}}
              </p>
            </li>
          </ul>
          <div class="no-rating" v-show="!food.ratings || !food.ratings.length">
            暂无评价
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Vue from 'vue';
  import {formatDate} from '../../common/js/date';
  import BScroll from 'better-scroll';
  import split from '../split/split';
  import ratingselect from '../ratingselect/ratingselect';
  import cartcontrol from '../cartcontrol/cartcontrol';

  const ALL = 2;
  export default {
      props: {
        food: {
          type: Object
        }
      },
      data() {
        return {
          showFlag: false,
          selectType: ALL,
          onlyContent: true,
          desc: {
            all: '全部',
            positive: '推荐',
            negative: '吐槽'
          }
        };
      },
      methods: {
        show() {
          this.showFlag = true;
          this.selectType = ALL;
          this.onlyContent = true;
          this.$nextTick(() => {
            if (!this.scroll) {
              this.scroll = new BScroll(this.$refs.food, {
                click: true
              });
            } else {
              this.scroll.refresh();
            }
          });
        },
        hide() {
          this.showFlag = false;
        },
        addFirst(event) {
          if (!event._constructed) {
            return;
          }
          Vue.set(this.food, 'count', 1);
        },
        needShow(type, text) {
          if (this.onlyContent && !text) {
            return false;
          }
          if (this.selectType === ALL) {
            return true;
          } else {
            return type === this.selectType;
          }
        }
      },
      events: {
        'ratingtype.select'(type) {
          this.selectType = type;
          this.$nextTick(() => {
            this.scroll.refresh();
          });
        },
        'content.toggle'(onlyContent) {
          this.onlyContent = onlyContent;
          this.$nextTick(() => {
            this.scroll.refresh();
          });
        }
      },
      filters: {
        formatDate(time) {
          let date = new Date(time);
          return formatDate(date, 'yyyy-MM-dd hh:mm');
        }
      },
      components: {
        cartcontrol,
        split,
        ratingselect
      }
  };
</script>

<style >
  .food{
    position:fixed;
    left:0;
    top:0;
    bottom:48px;
    z-index:30;
    width:100%;
    background:#fff;
  }
  .image-header{
    position:relative;
    width:100%;
    height:0;
    padding-top:100%;
  }
  .image-header img{
    position:absolute;
    width:100%;
    height:100%;
    top:0;
    left:0;
  }
  .image-header .back{
    position:absolute;
    top:10px;
    left:0;

  }
  .image-header .back .icon-arrow_lift{
    display:block;
    padding:10px;
    font-size:20px;
    color:#fff;
  }
  .food-content  .content{
    padding:18px;
    position:relative;
  }
  .food-content  .content .title{
    line-height: 14px;
    margin-bottom:8px;
    font-size:14px;
    font-weight:700px;
    color:rgb(7,17,27);
  }
  .food-content  .content .details{
    margin-bottom:18px;
    line-height:10px;
    font-size:0 ;
    height:10px;
  }
  .food-content  .content .details .sell-count{
    color:rgb(147,153,159);
    font-size:10px;
    margin-right:12px;
  }
  .food-content .content .details .rating{
    color:rgb(147,153,159);
    font-size:10px;

  }
  .food-content  .content .price{
    font-weight:700;
    font-size:24px;
  }
  .food-content  .content .price .now{
    margin-right:8px;
    font-size:14px;
    color:rgb(240,20,20);
  }
  .food-content  .content .price .old{
    text-decoration:line-through;
    font-size:10px;
    color:rgb(147,153,159);
  }
  .food-content .cartcontrol-wrapper{
    position:absolute;
    right:12px;
    bottom:12px;
  }
  .food-content .buy{
    position:absolute;
    right:9px;
    bottom:9px;
    z-index:10;
    height:24px;
    line-height:24px;
    padding:0 12px;
    box-sizing:border-box;
    font-size:10px;
    border-radius:12px;
    color:#fff;
    background:rgb(0,160,220);
  }
  .food-content .info{
    padding:18px;

  }
  .food-content .info .title{
    margin-bottom:6px;
    font-size:14px;
    line-height:14px;
    color:rgb(7,17,27);
  }
  .food-content .info .text{
    line-height:24px;
    padding:0 8px;
    font-size:12px;
    color:#4d555d;
  }
  .rating {
    padding-top:18px;

  }
  .rating .title{
    margin-left:18px;
    font-size:14px;
    line-height:14px;
    color:rgb(7,17,27);
  }
  .rating .rating-wrapper{
    padding:0 18px;
  }
  .rating .rating-wrapper .rating-item{
    position:relative;
    padding:16px 0;
    border-bottom:1px solid rgba(7,17,27,0.1);
  }
  .rating .rating-wrapper .rating-item .user{
    position:absolute;
    right:0;
    top:16px;
    font-size:0;
    line-height:12px;

  }
  .rating .rating-wrapper .rating-item .name{
    display:inline-block;
    vertical-align:top;
    font-size:10px;
    margin-right:6px;
    color:rgb(147,153,159);
  }
  .rating .rating-wrapper .rating-item .avatar{
    border-radius:50%;
  }
  .rating .rating-wrapper .rating-item .time{
    line-height:12px;
    margin-bottom:6px;
    font-size:10px;
    color:rgb(147,153,159);
  }
  .rating .rating-wrapper .rating-item .text{
    line-height:16px;
    font-size:12px;
    color:rgb(7,17,27);
  }
  .rating .rating-wrapper .rating-item .icon-thumb_up,
  .rating .rating-wrapper .rating-item .icon-thumb_down{
    line-height:10px;
    margin-right:4px;
    font-size:12px;
    border-radius:50%;
    width:16px;
    height:8px;
    display:inline-block;
    background:rgb(0,160,220);
    color:#fff;
    text-align:center;
    padding:4px 0;
    vertical-align:top;
  }
  .rating .rating-wrapper .rating-item .icon-thumb_down{
    background:#999;
  }
  .no-rating {
    padding:16px 0;
    font-size:12px;
    color:rgb(147,153,159);
  }
</style>



