<template>
  <div>
  <div class="goods">
    <div class="menu-wrapper" ref="menuWrapper">
      <ul>
        <li v-for="(item,index) in goods" class="menu-item" :class="{'current':currentIndex===index}"
        @click="selectMenu(index,$event)">
          <span class="text">
            <span v-show="item.type>0" class="icon"
                 :class="classMap[item.type]"></span>
            {{item.name}}
          </span>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="foodsWrapper">
      <ul>
        <li v-for="item in goods" class="food-list food-list-hook">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li @click="selectFood(food,$event)" v-for="food in item.foods" class="food-item">
              <div class="icon">
                <img width="57" :src="food.icon">
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span>月售{{food.sellCount}}份</span>
                  <span>好评率{{food.rating}}%</span>
                </div>
                <div class="price">
                  <span class="now">￥{{food.price}}</span>
                  <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food="food"></cartcontrol>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shopcart :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice">

    </shopcart>
  </div>
  <food :food="selectedFood" ref='food'></food>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll';
  import shopcart from '../shopcart/shopcart';
  import food from '../food/food';
  import cartcontrol from '../cartcontrol/cartcontrol';
  const ERR_OK = 0;
  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data() {
      return {
        goods: [],
        listHeight: [],
        scrollY: 0,
        selectedFood: {}
      };
    },
    computed: {
      currentIndex() {
        for (let i = 0; i < this.listHeight.length; i++) {
          let height1 = this.listHeight[i];
          let height2 = this.listHeight[i + 1];
          if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
            return i;
          }
        }
        return 0;
      },
      selectFoods() {
        let foods = [];
        this.goods.forEach((good) => {
          good.foods.forEach((food) => {
            if (food.count) {
              foods.push(food);
            }
          });
        });
        return foods;
      }
    },
    created() {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
      this.$http.get('/api/goods').then((response) => {
        response = response.body;
        if (response.errno === ERR_OK) {
          this.goods = response.data;
          console.log(this.goods);
          this.$nextTick(() => {
            this._initScroll();
            this._calculateHeight();
          });
        }
      });
    },
    methods: {
      selectMenu(index, event) {
        if (!event._constructed) {
          return;
        }
        let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
        let el = foodList[index];
        this.foodsScroll.scrollToElement(el, 300);
        console.log(index);
      },
      selectFood(food, event) {
        if (!event._constructed) {
          return;
        }
        this.selectedFood = food;
        this.$refs.food.show();
      },
      _initScroll() {
        this.menuScroll = new BScroll(this.$refs.menuWrapper, {
          click: true
        });
        this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
          click: true,
          probeType: 3
        });
        this.foodsScroll.on('scroll', (pos) => {
          this.scrollY = Math.abs(Math.round(pos.y));
        });
      },
      _calculateHeight() {
        let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
        let height = 0;
        this.listHeight.push(height);
        for (let i = 0; i < foodList.length; i++) {
          let item = foodList[i];
          height += item.clientHeight;
          this.listHeight.push(height);
        }
      }
    },
    components: {
      shopcart,
      cartcontrol,
      food
    }
  };
</script>

<style >
  .goods{
    display:flex;
    position:absolute;
    top:174px;
    bottom:46px;
    width:100%;
    overflow:hidden;
  }
  .goods>.menu-wrapper{
    flex:0 0 80px;
    width:80px;
    background:#f3f5f7;
  }
  .goods>.menu-wrapper .menu-item{
    display:table;
    height:54px;
    width:56px;
    line-height:14px;
    padding:0 12px;
    border-bottom:1px solid rgba(7,17,27,0.2);
  }
  .goods>.menu-wrapper .menu-item.current{
    position:relative;
    z-index:10;
    margin-top:-1px;
    background:#fff;
    font-weight:700
  }
  .goods>.menu-wrapper .menu-item .icon{
    display:inline-block;
    width:12px;
    height:12px;
    margin-right:2px;
    background-size:12px;
    background-repeat:no-repeat;
    vertical-align:top;
  }
  .decrease{
    background-image:url('../header/decrease_1@2x.png');
  }
  .discount{
    background-image:url('../header/discount_1@2x.png');
  }
  .guarantee{
    background-image:url('../header/guarantee_1@2x.png');
  }
  .invoice{
    background-image:url('../header/invoice_1@2x.png');
  }
  .special{
    background-image:url('../header/special_1@2x.png');
  }
  .goods>.menu-wrapper .menu-item .text{
    display:table-cell;
    vertical-align:middle;
    width:56px;
    font-size:14px;
  }
  .goods>.foods-wrapper{
    flex:1;
  }
  .goods>.foods-wrapper .title{
    padding-left:14px;
    height:26px;
    line-height:26px;
    border-left:2px solid #d9dde1;
    font-size:12px;
    color:rgb(14,153,159);
    background:#f3f5f7;
  }
  .goods>.foods-wrapper .food-item{
    display:flex;
    margin:18px;
    padding-bottom:18px;
    border-bottom:1px solid rgba(7,17,17,0.1);
    position:relative;
  }
  .goods>.foods-wrapper .food-item:last-child{
    border-bottom:none;
    margin-bottom:0;
  }
  .goods>.foods-wrapper .icon{
    flex:0 0 57px;
    margin-right:10px;
  }
  .goods>.foods-wrapper .content{
    flex:1;
  }
  .goods>.foods-wrapper .content .name{
    margin:2px 0 8px 0;
    height:14px;
    line-height:14px;
    font-size:14px;
    color:rgb(7,17,27);
  }
  .goods>.foods-wrapper .content .desc{
    line-height:10px;
    margin-bottom:8px;
    font-size:10px;
    color:rgb(147,153,159);
  }
  .goods>.foods-wrapper .content .extra{
    line-height:10px;
    font-size:10px;
    color:rgb(147,153,159);
  }
  .goods>.foods-wrapper .content .count{
    margin-right:12px;
  }
  .goods>.foods-wrapper .content .price{
    font-weight:700;
    font-size:24px;
  }
  .goods>.foods-wrapper .content .price .now{
    margin-right:8px;
    font-size:14px;
    color:rgb(240,20,20);
  }
  .goods>.foods-wrapper .content .price .old{
    text-decoration:line-through;
    font-size:10px;
    color:rgb(147,153,159);
  }
  .cartcontrol-wrapper{
    position:absolute;
    right:0;
    bottom:12px;
  }
</style>



