<template>
  <div class="ratingselect">
    <div class="rating-type">
      <span @click="select(2,$event)" class="block positive" :class="{'active':selectType===2}">{{desc.all}}<span class="count">{{ratings.length}}</span></span>
      <span @click="select(0,$event)"  class="block positive" :class="{'active':selectType===0}">{{desc.positive}}<span class="count">{{positives.length}}</span></span>
      <span @click="select(1,$event)"  class="block negative" :class="{'active':selectType===1}">{{desc.negative}}<span class="count">{{negatives.length}}</span></span>
    </div>
    <div @click="toggleContent" class="switch" :class="{'on':onlyContent}">
      <span class="icon-check_circle">*</span>
      <span class="text">只看有内容的评价</span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  const POSITIVE = 0;
  const NEGATIVE = 1;
  const ALL = 2;
  export default {
    props: {
      ratings: {
        type: Array,
        default() {
          return [];
        }
      },
      selectType: {
        type: Number,
        default: ALL
      },
      onlyContent: {
        type: Boolean,
        default: false
      },
      desc: {
        type: Object,
        default() {
          return {
            all: '全部',
            positive: '满意',
            negative: '不满意'
          };
        }
      }
    },
    data: function () {
      return {
        myselectType: this.selectType,
        myonlyContent: this.onlyContent
      };
    },
    computed: {
      positives() {
        return this.ratings.filter((rating) => {
          return rating.rateType === POSITIVE;
        });
      },
      negatives() {
        return this.ratings.filter((rating) => {
          return rating.rateType === NEGATIVE;
        });
      }
    },
    methods: {
      select(type, event) {
        if (!event._constructed) {
          return;
        }
        this.selectType = type;
        this.$on('ratingtype.select', type);
      },
      toggleContent(event) {
        if (!event._constructed) {
          return;
        }
        this.onlyContent = !this.onlyContent;
        this.$on('content.toggle', this.onlyContent);
      }
    }
  };
</script>

<style>
  .ratingselect{

  }
  .ratingselect .rating-type{
    padding:18px 0;
    margin:0 18px;
    border-bottom:1px solid rgba(7,17,27,0.1);

  }
  .ratingselect .rating-type .block{
    display:inline-block;
    padding:8px 12px;
    margin-right:8px;
    border-radius: 1px ;
    color:rgb(77,85,93);
    font-size:12px;
    line-height:16px;
  }
  .ratingselect .rating-type .block.active{
    color:#fff;
  }
  .ratingselect .rating-type .count{
    font-size:8px;
    margin-left:2px;

  }
  .ratingselect .rating-type .positive{
    background:rgba(0,160,220,0.2);
  }
  .ratingselect .rating-type .positive.active{
    background:rgb(0,160,220);
  }
  .ratingselect .rating-type .negative{
    background:rgba(77,85,93,0.2);
  }
  .ratingselect .rating-type .negative.active{
    background:rgb(77,85,93);
  }
  .ratingselect .switch{
    padding:12px 18px;
    line-height:24px;
    border-bottom:1px solid rgba(7,17,27,0.1);
    color:rgb(147,153,159);
  }
  .ratingselect .switch .icon-check_circle{
    font-size:24px;
    margin-right:4px;
    border-radius:50%;
    width:24px;
    height:12px;
    display:inline-block;
    background:#999;
    color:#fff;
    text-align:center;
    line-height:24px;
    padding:6px 0;
    vertical-align:top;
  }
  .ratingselect .switch.on .icon-check_circle{
    background:#00c850;
    color:#fff;
  }
  .ratingselect .switch .text{
    font-size:12px;
    line-height:12px;
  }
</style>



